// In SmallholderFarmerDashboard.jsx - UPDATE to use dashboardAPI

import React, { useState, useEffect } from 'react';
import { useAuth } from "../../contexts/AuthContext.jsx";
import { Card, Button, LoadingSpinner, Alert, WeatherWidget, MarketPriceTicker } from '@agro-gram/ui';
import { useDashboardHandlers } from '../../hooks/useDashboardHandlers';
import { dashboardAPI } from '@agro-gram/api'; // ADD THIS IMPORT
import './SmallholderFarmerDashboard.css';

const SmallholderFarmerDashboard = () => {
  const { user } = useAuth();
  const [dashboardData, setDashboardData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('today');

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      // FIXED: Use the dashboardAPI service
      const response = await dashboardAPI.getSmallholderFarmerDashboard();
      
      if (response.success) {
        setDashboardData(response.data);
      } else {
        console.warn('Dashboard API returned fallback data');
        setDashboardData(getFallbackDashboardData());
      }
    } catch (error) {
      console.error('Error fetching dashboard:', error);
      setDashboardData(getFallbackDashboardData());
    } finally {
      setLoading(false);
    }
  };

  // Fallback data function
  const getFallbackDashboardData = () => ({
    dashboard_type: "smallholder_farmer",
    welcome_message: `Welcome, ${user?.first_name || 'Farmer'}!`,
    today_priorities: [
      { time: 'Morning', description: 'Check soil moisture', priority: 'High' },
      { time: 'Afternoon', description: 'Water tomato plants', priority: 'Medium' },
      { time: 'Evening', description: 'Record harvest data', priority: 'Low' }
    ],
    active_crops: [
      { icon: '', name: 'Maize', progress: 65, status: 'growing' },
      { icon: '', name: 'Tomatoes', progress: 30, status: 'planted' },
      { icon: '', name: 'Beans', progress: 95, status: 'ready' }
    ],
    market_prices: [
      { crop: 'Maize', price: 45.50, unit: 'kg', trend: 'up' },
      { crop: 'Tomatoes', price: 68.20, unit: 'kg', trend: 'stable' },
      { crop: 'Beans', price: 85.75, unit: 'kg', trend: 'up' }
    ],
    immediate_alerts: [],
    stats: {
      total_farms: 1,
      active_crops_count: 3,
      pending_tasks: 2,
      revenue_this_month: 1250.50
    },
    quick_actions: [
      { title: "Record Harvest", icon: "", url: "/farms" },
      { title: "Check Prices", icon: "", url: "/marketplace" },
      { title: "Get Advice", icon: "", url: "/recommendations" }
    ]
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const {
    handleRefresh,
    handleVoice,
    handleQuickAction,
    handleExport,
  } = useDashboardHandlers(dashboardData, fetchDashboardData, setDashboardData, setLoading);

  if (loading) return <div className="dashboard-loading"><LoadingSpinner size="large" /></div>;

  return (
    <div className="dashboard smallholder-farmer-dashboard">
      <div className="dashboard-header mobile-optimized">
        <div className="header-content">
          <h1>Smallholder Farmer Dashboard</h1>
          <p>Simple tools for daily farm management</p>
        </div>
        <div className="header-actions">
          <Button size="small" variant="outline" onClick={() => handleVoice("Welcome to your dashboard")}>
             Voice Command
          </Button>
          <Button size="small" variant="ghost" onClick={handleRefresh}> Refresh</Button>
        </div>
      </div>

      {dashboardData?.immediate_alerts?.length > 0 && (
        <div className="alerts-section">
          {dashboardData.immediate_alerts.map((alert, index) => (
            <Alert key={index} type={alert.type} className="immediate-alert">
              <strong>{alert.title}:</strong> {alert.message}
            </Alert>
          ))}
        </div>
      )}

      <div className="dashboard-grid mobile-grid">
        <Card className="priority-card touch-optimized">
          <div className="card-header">
            <h3> Today's Priorities</h3>
            <Button size="small" variant="ghost" onClick={handleRefresh}>Refresh</Button>
          </div>
          <div className="task-list">
            {dashboardData?.today_priorities?.map((task, index) => (
              <div key={index} className="task-item">
                <div className="task-time">{task.time}</div>
                <div className="task-content">
                  <span className="task-description">{task.description}</span>
                  <span className="task-priority">{task.priority}</span>
                </div>
                <Button size="small" onClick={() => handleQuickAction('log_activity', { task })}>Start</Button>
              </div>
            ))}
          </div>
        </Card>

        <Card className="crops-card touch-optimized">
          <div className="card-header">
            <h3> Active Crops</h3>
            <Button size="small" variant="ghost" onClick={() => handleQuickAction('manage_crops')}>Manage</Button>
          </div>
          <div className="crops-visual">
            {dashboardData?.active_crops?.map((crop, index) => (
              <div key={index} className="crop-visual-item">
                <div className="crop-icon">{crop.icon}</div>
                <div className="crop-info">
                  <h4>{crop.name}</h4>
                  <div className="crop-progress">
                    <div className="progress-bar">
                      <div className="progress-fill" style={{width: `${crop.progress}%`}}></div>
                    </div>
                    <span>{crop.progress}%</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </Card>

        <Card className="analytics-card">
          <div className="card-header">
            <h3> Quick Analytics</h3>
            <Button size="small" onClick={() => handleExport('csv')}>Export</Button>
          </div>
          <div className="analytics-grid">
            <div className="metric-card">
              <div className="metric-value">${dashboardData?.stats?.revenue_this_month || 0}</div>
              <div className="metric-label">This Month</div>
              <div className="metric-trend positive">+12%</div>
            </div>
            <div className="metric-card">
              <div className="metric-value">{dashboardData?.stats?.active_crops_count || 0}</div>
              <div className="metric-label">Active Crops</div>
            </div>
            <div className="metric-card">
              <div className="metric-value">{dashboardData?.stats?.pending_tasks || 0}</div>
              <div className="metric-label">Pending Tasks</div>
            </div>
          </div>
        </Card>

        <Card className="prices-card">
          <div className="card-header">
            <h3> Market Prices</h3>
            <Button size="small" variant="ghost" onClick={() => handleQuickAction('check_prices')}>View All</Button>
          </div>
          <MarketPriceTicker prices={dashboardData?.market_prices || []} />
        </Card>

        <Card className="weather-card">
          <WeatherWidget location={user?.location} />
          <Button size="small" fullWidth onClick={() => handleQuickAction('check_weather')}>Detailed Forecast</Button>
        </Card>

        <Card className="actions-card touch-optimized">
          <div className="card-header">
            <h3> Quick Actions</h3>
          </div>
          <div className="actions-grid">
            <Button size="large" variant="outline" fullWidth onClick={() => handleQuickAction('log_activity')}> Log Activity</Button>
            <Button size="large" variant="outline" fullWidth onClick={() => handleQuickAction('check_prices')}> Check Prices</Button>
            <Button size="large" variant="outline" fullWidth onClick={() => handleQuickAction('check_weather')}> Weather</Button>
            <Button size="large" variant="outline" fullWidth onClick={() => handleQuickAction('sell_produce')}> Sell Produce</Button>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default SmallholderFarmerDashboard;